document.addEventListener('DOMContentLoaded', function () {
    'use strict';
    utils.translator(document.body);
    function set_btns_url(tab) {
        var current_url = tab.url;
        var current_domain = utils.extract_hostname(current_url);
        var current_domain_url;
        if ('https' === tab.url.substring(0, 5)) {
            current_domain_url = 'https://' + current_domain;
        }
        else {
            current_domain_url = 'http://' + current_domain;
        }
        document.getElementById('wp_site_url').value = "define( 'WP_SITEURL', '" + current_domain_url + "' );";
        document.getElementById('wp_home').value = "define( 'WP_HOME', '" + current_domain_url + "' );";
        document.getElementById('wp_cookie_domain').value = "define( 'COOKIE_DOMAIN', '" + current_domain + "' );";
    }
    utils.get_current_tab(set_btns_url);
    document.getElementById('post_autosave').addEventListener('change', function () {
        document.getElementById('post_autosave_text').value = "define( 'AUTOSAVE_INTERVAL', " + this.value + " );";
    });
    document.getElementById('post_revisions').addEventListener('change', function () {
        document.getElementById('post_revisions_text').value = "define( 'WP_POST_REVISIONS', " + this.value + " );";
    });
    document.getElementById('disable_file_editor').addEventListener('change', function () {
        document.getElementById('wp_disable_file_editor').value = "define( 'DISALLOW_FILE_EDIT', " + this.value + " );";
    });
    document.getElementById('ssl_login').addEventListener('change', function () {
        document.getElementById('wp_ssl_login').value = "define( 'FORCE_SSL_LOGIN', " + this.value + " );";
    });
    document.getElementById('ssl_admin').addEventListener('change', function () {
        document.getElementById('wp_ssl_admin').value = "define( 'FORCE_SSL_ADMIN', " + this.value + " );";
    });
    document.getElementById('php_front_memory').addEventListener('change', function () {
        document.getElementById('php_front_memory_text').value = "define( 'WP_MEMORY_LIMIT', '" + this.value + "' );";
    });
    document.getElementById('php_back_memory').addEventListener('change', function () {
        document.getElementById('php_back_memory_text').value = "define( 'WP_MAX_MEMORY_LIMIT', '" + this.value + "' );";
    });
    utils.show_data_div();
});
